@import (less) "color.less";
.accordion-arrow {
  cursor: pointer;
  font-size: 1.6em;
}

.align-right {
  text-align: right;
}

.container-row {
  display: table-row;
  float: left !important;
}

.container-ident-cliente {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 0.875em;
  color: inherit;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 78%;
  font-weight: normal;
  float: left;
  padding-top: 6px;
  padding-left: 7px;
  padding-right: 7px;
  padding-bottom: 7px;
  margin-left: -3.3px;
}

.full-width {
  width: 100%;
}

.voce-ident-cliente {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1em;;
  color: inherit;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: normal;
}

.border-pannel{
  border: 1px solid #797979 !important;
}

.testo-ident-cliente {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 1em;
  color: inherit;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-weight: bold;
}

.panel-body {
  font-family: Arial;
  font-weight: 400;
  font-style: normal;
  font-size: 0.81em;
  width: 100%;
  border-top: 1px solid gray;
  ;
  vertical-align: middle;
  z-index: 90;
  cursor: pointer;
  text-align: left;
  color: #CCCCCC;
  /*font-weight: bold;*/
}

.panel-body:hover {
  background-color: gray;
  color: @white;
}

.info-cliente-title {
  font-family: Arial;
  // font-weight: 400;
  font-style: normal;
  font-size: 14px;
  color: #cccccc;
  padding: 10px 0 10px 0;
  border-bottom: 0px solid gray;
  margin-left: auto;
  margin-right: auto;
}

.info-cliente-open {
  position: relative;
  background-color: @darkest_gray;
  z-index: 90;
  border: 0;
  border-radius: 0;
  text-align: center;
  margin-top: 0.5625em;
  display: table;
  color: @white;
  width: 22.25em;
  margin-left: -6px;
}

.container-arrow {
  float: right;
  padding-top: 0.9em;
  // padding-right: 1.5625em;
  padding-right: 1.375em;
}

.triangolo {
  width: 0;
  height: 0;
  z-index: 91;
  border-left: 0.563em solid transparent;
  border-right: 0.563em solid transparent;
  border-bottom: 0.563em solid @darkest_gray;
  float: right;
  margin-right: 1.96em;
  position: relative;
  top: 1px;
}

.triangolo-border {
  width: 0;
  height: 0;
  z-index: 90;
  border-left: 0.625em solid transparent;
  border-right: 0.625em solid transparent;
  border-bottom: 0.625em solid #797979;
  float: right;
  margin-right: 0;
  position: relative;
  top: 0px;
  left: 19px;
  margin-bottom: -1px;
}

.triangolo-hide {
  width: 0;
  height: 0;
  z-index: 90;
  border-left: 0.563em solid transparent;
  border-right: 0.563em solid transparent;
  border-bottom: 0.563em solid transparent;
  float: right;
  margin-right: 1.75em;
  position: relative;
  top: 1px;
}

.icon-select{
  color:#1ed31e; 
  display: block; 
  float: right; 
  position: relative; 
  top: -0.56em;
}

.selected-client {
  background-color: #444444;
  color: rgba(255, 255, 255, 1); 
  font-weight: bold;
}

:host-context(.contrasto-on) {
  
  .border-pannel{
    border: 1px solid @white !important;
  }
  
  .panel-body {
    border-top: 1px solid @white;
    color: @white;
  }
  
  .panel-body:hover {
    background-color: @white;
    color: @black;
    border-color: @black;
  }
  
  .info-cliente-title {
    color: @white;
    border-bottom: 0px solid @white;
  }

  .info-cliente-open {
    background-color: @black;
  }
  
  .triangolo {
    border-bottom-color: @black;
  }  

  .triangolo-border {
    border-bottom: 0.625em solid @white;
  }

  .icon-select{
    color:@black;
  }
  
  .selected-client {
    background-color: @white;
    color: @black; 
    font-weight: bold;
    &.panel-body {
      border-color: @black;
    }
  }

}